export default {
	props:{
		type:{
			type:Number||String,
			default:0
		}
	},
	data() {
		return {
			dataList: []
		}
	},
	methods: {
		groupDetails(v) {
			this.$api.navTo(`/pages/groupInfo?id=${v.id}&name=${v.groupName}`)
			// this.$api.navTo(`/pages/groupInfo`)
		},
		setData(val) {
			val.forEach(item => {
				this.dataList.push(item)
			})
		},
		initData() {
			this.dataList = []
		}
	}
}
